import utils from '../../utils';

export const base = theme => {
  const { w, h } = utils.getDimensions(theme, {
    ratioX: 16,
    ratioY: 9,
    upCount: 3
  });
  return {
    expandedWidth: utils.getWidthByColumnSpan(theme, 8),
    expandedHeight: h,
    imageSize: { width: w, height: h },
    metadata: { descriptionTextStyle: { maxLines: 3 } }
  };
};
